@import url(https://fonts.googleapis.com/css?family=Montserrat:light,regular,medium,semibold,bold&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  color: #FFFFFF;
  align-items: center;
  word-wrap: break-word;
  font-family: 'Montserrat', san-serif;
}

.title {
  color: #FFFFFF;
  word-wrap: break-word;
  text-align: center;
  font-weight: 400;
  margin-bottom: 20px;
  font-size: 48px;
}

.prime-number-checker {
  margin: 40px 0;
  text-align: center;
  background: #FFFFFF2e;
  border-radius: 6px;
  padding: 50px;
}

.prime-number-checker h2 {
  font-size: 40px;
}

h1, h2 {
  margin: 0;
}

.description {
  font-size: 20px;
  font-weight: 300;
}

.result {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 0;
}

.btns {
  margin: 15px 0;
}

.prime-check-btn {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0px 5px;
  border: #FFFFFF solid 1.5px;
  background: none;
  color: #FFFFFF;
  font-weight: 500;
  cursor: pointer;
  border-radius: 4px;
}

.change-color-btn {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: none;
  background: white;
  color: #FFFFFF;
  font-weight: 500;
  cursor: pointer;
  border-radius: 4px;
  font-weight: 600;
}

button {
  box-shadow: 0 5px rgba(0,0,0,0.2);
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  transition: box-shadow 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
  transition: transform 0.1s ease-in-out, box-shadow 0.1s ease-in-out;
  transition: transform 0.1s ease-in-out, box-shadow 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
  outline: none;
  font-family: 'Montserrat', san-serif;
  font-size: 20px;
  padding: 15px 20px;
}

button:hover {
  opacity: 0.6;
}

button:active {
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
  box-shadow: 0 0 rgba(0,0,0,0.2);
}

